import PresentationStep2 from "@/components/presentation-step2";
import Step from "@/components/step";
import Wechat from "@/components/wechat";
import { base_url } from "@/utils/const";
import classNames from "classnames";
import Head from "next/head";
import { useEffect, useState } from "react";

const prevUrl = base_url + "";
const nextUrl = base_url + "/step-3/";

export default function Step2() {
  const translated = useTranslate();
  return (
    <main>
      <Head>
        <title>沉浸式翻译 - 翻译网页</title>
      </Head>
      <div className={classNames("card")}>
        <h1 className={classNames("title", "notranslate")}>
          👻 试试翻译一下当前页面
        </h1>
        <Step step={2} />
        <PresentationStep2 />
        <footer className={classNames("notranslate")}>
          <a
            id="prev"
            href={prevUrl}
            className={classNames("secondary", "underline")}
          >
            上一步
          </a>
          <link rel="prefetch" href={prevUrl} />
          <div className={classNames("right-area")}>
            <a
              id="next"
              href={translated ? nextUrl : "javascript:void(0);"}
              className={classNames("ghost", { "disabled": !translated })}
              data-href={nextUrl}
            >
              {translated ? "🎉 翻译成功！点击开始使用" : "请先点击翻译按钮"}
            </a>
            <link rel="prefetch" href={nextUrl} />
            <a
              id="skip"
              className={classNames("secondary")}
              hidden={translated}
              href={nextUrl}
            >
              跳过，直接进入下一步
            </a>
          </div>
        </footer>
      </div>
      <Wechat />
    </main>
  );
}

function useTranslate() {
  const [translated, setTransalted] = useState(false);
  useEffect(() => {
    const listenKey = "immersiveTranslateDocumentMessageTellThirdParty";
    const listener = (e: any) => {
      const { detail } = e;
      if (detail) {
        try {
          const detailObj = JSON.parse(detail);
          if (detailObj.type === "paragraphTranslated") {
            const { ok } = detailObj.payload;
            setTransalted(ok);
          } else {
          }
        } catch (e) {
          console.error(e);
        }
      }
    };
    document.addEventListener(listenKey, listener);
    return () => {
      document.removeEventListener(listenKey, listener);
    };
  }, []);
  return translated;
}
